/* global angular */
angular.module('PricePoint')
  .controller('AgentProfileController', ['$scope', '$routeParams', 'CalculatorData',
    function($scope, $routeParams, CalculatorData) {
      $scope.serviceType = $routeParams.serviceType;
      $scope.isFreight = $routeParams.serviceType == 'Freight';
      CalculatorData.getAgentDetails($routeParams.agentId, $routeParams.serviceType, function(data) {
        $scope.agentDetails = data;
        $scope.documents = $scope.agentDetails.documents.filter(function(agentDocument) {
          return ($scope.isFreight == agentDocument.isFreight) && !agentDocument.isArchived;
        }).map(function(agentDocument) {
          agentDocument.name = agentDocument.uploadPath.split('-').pop();
          return agentDocument;
        });
      });
      if (!$scope.isFreight) {
        CalculatorData.getTariffSupplements($routeParams.agentId, $routeParams.marketId, function(data) {
          $scope.supplements = data;
        });
        CalculatorData.getMarketDetails($routeParams.marketId, function(data) {
          $scope.market = data;
        });
      }
    }
  ]);