/* global angular */
angular.module('PricePoint')
  .controller('CalculatorInitialViewController', ['$scope', '$routeParams', 'CalculatorData', '$mdDialog', '$location',
    function($scope, $routeParams, CalculatorData, $mdDialog, $location) {

      $scope.volumeUnitOptions = CalculatorData.volumeUnitOptions;
      $scope.weightUnitOptions = CalculatorData.weightUnitOptions;
      $scope.tariffTypeOptions = CalculatorData.tariffTypeOptions;
      $scope.containerSizeOptions = CalculatorData.containerSizeOptions;
      $scope.locationChanged = false;
      $scope.currencies = CalculatorData.currencies.query(function(data) {
        $scope.currencies = data;
      });
      $scope.quoteRequested = {
        originLocation: null,
        destinationLocation: null,
        volume: 0,
        weight: 0,
        weightUnits: 'lb',
        volumeUnits: 'cft',
        tariffType: 'FCL_L',
        containerSize: '20',
        currencyId: 1,
        accountId: null,
      };
      $scope.ports = CalculatorData.ports.query(function(data) {
        $scope.ports = data;
      });
      $scope.associations = CalculatorData.associations.query(function(data) {
        $scope.associations = data;
        $scope.selectedAssociations = data.reduce ? data.reduce(function(associations, association) {
          associations[association] = {
            selected: true
          };
        }) : {};
      });

      $scope.certifications = CalculatorData.certifications.query(function(data) {
        $scope.certifications = data;
        $scope.selectedCertifications = data.reduce ? data.reduce(function(certifications, certification) {
          certifications[certification] = {
            selected: true
          };
        }) : {};
      });

      $scope.accounts = CalculatorData.getAccounts(function(data) {
        $scope.accounts = data;
      });

      $scope.selectedOriginPorts = null;
      $scope.selectedDestinationPorts = null;
      $scope.originAgents = null;
      $scope.destinationAgents = null;
      $scope.freightAgents = null;

      function _assignAgents(data, agents, changeInSelection) {
        if (changeInSelection && $scope.originAgents) {
          var previousIds = Object.keys(agents),
            previouslySelectedIds = previousIds
            .filter(function(id) {
              return agents[id].selected;
            });
          agents = data;
          for (var id in agents) {
            agents[id].selected = (previousIds.indexOf(id) == -1 || previouslySelectedIds.indexOf(id) > -1);
          }
        }
        else {
          agents = data;
        }
        return agents;
      }

      function _setAgentsOnOriginChange(newValue, changeInSelection) {
        if (newValue) {
          CalculatorData.getOriginAgents($scope, function(data) {
            $scope.originAgents = _assignAgents(data, $scope.originAgents, changeInSelection);
          });
          CalculatorData.getFreightAgents($scope, function(data) {
            $scope.freightAgents = _assignAgents(data, $scope.freightAgents, changeInSelection);
          });
        }
        else {
          $scope.freightAgents = null;
          $scope.originAgents = null;
        }
      }

      function _setAgentsOnDestinationChange(newValue, changeInSelection) {
        if (newValue) {
          CalculatorData.getDestinationAgents($scope, function(data) {
            $scope.destinationAgents = _assignAgents(data, $scope.destinationAgents, changeInSelection);
          });
          CalculatorData.getFreightAgents($scope, function(data) {
            $scope.freightAgents = _assignAgents(data, $scope.freightAgents, changeInSelection);
          });
        }
        else {
          $scope.freightAgents = null;
          $scope.destinationAgents = null;
        }
      }

      $scope.$watch('quoteRequested.originLocation.Ports', function(newValue, oldValue) {
        if (newValue != oldValue) {
          $scope.selectedOriginPorts = newValue ? newValue.reduce(function(ports, port) {
            ports[port] = {
              selected: true
            };
            return ports;
          }, {}) : null;
          _setAgentsOnOriginChange(newValue);
        }
      });

      $scope.selectedDestinationPorts = null;
      $scope.$watch('quoteRequested.destinationLocation.Ports', function(newValue, oldValue) {
        if (newValue != oldValue) {
          $scope.selectedDestinationPorts = newValue ? newValue.reduce(function(ports, port) {
            ports[port] = {
              selected: true
            };
            return ports;
          }, {}) : null;
          _setAgentsOnDestinationChange(newValue);
        }
      });

      $scope.$watch('selectedDestinationPorts', function(newValue, oldValue) {
        if (newValue != oldValue) {
          _setAgentsOnDestinationChange(newValue, true);
        }
      }, true);

      $scope.$watch('selectedOriginPorts', function(newValue, oldValue) {
        if (newValue != oldValue) {
          _setAgentsOnOriginChange(newValue, true);
        }
      }, true);

      $scope.$watch('quoteRequested.tariffType', function(newValue, oldValue) {
        if (newValue != oldValue) {
          if ($scope.quoteRequested.originLocation)
            _setAgentsOnOriginChange(newValue, true);
          if ($scope.quoteRequested.destinationLocation)
            _setAgentsOnDestinationChange(newValue, true);
        }
      });

      $scope.calculate = function(event) {
        var selectOriginAgents = $scope.originAgents ? Object.keys($scope.originAgents)
          .filter(function(id) {
            return $scope.originAgents[id].selected;
          }) : [],
          selectedDestinationAgents = $scope.destinationAgents ? Object.keys($scope.destinationAgents)
          .filter(function(id) {
            return $scope.destinationAgents[id].selected;
          }) : [];

        if (selectOriginAgents.length || selectedDestinationAgents.length) {
          CalculatorData.setQuoteData({
            quoteRequested: $scope.quoteRequested,
            associations: $scope.associations,
            certifications: $scope.certifications,
            originAgents: $scope.originAgents,
            destinationAgents: $scope.destinationAgents,
            freightAgents: $scope.freightAgents,
            selectedDestinationPorts: $scope.selectedDestinationPorts,
            selectedOriginPorts: $scope.selectedOriginPorts,
          });
          $location.path('/calculator');
        }
        else {
          $mdDialog.show(
            $mdDialog.alert()
            .parent(angular.element(document.body))
            .title('Please select all required values')
            .content('Please select atleast one Origin Agent or Destination Agent.')
            .ariaLabel('Form Invalid Alert')
            .ok('Got it!')
            .targetEvent(event)
          );
        }
      };

    }
  ]);