/* global angular */
angular.module('PricePoint').factory('UserFactory', ['$http', 'AuthTokenFactory', '$q',
 
  function UserFactory($http, AuthTokenFactory, $q) {
    
    return {
      login: login,
      logout: logout,
      getUser: getUser,
    };

    function login(username, password) {
      return $http.post('/login', {
        username: username,
        password: password
      }).then(function success(response) {
        AuthTokenFactory.setToken(response.data.token);
        return response;
      });
    }

    function logout() {
      AuthTokenFactory.setToken();
    }

    function getUser() {
      if (AuthTokenFactory.getToken()) {
        return $http.get('/me');
      }
      else {
        return $q.reject({
          data: 'Not Authorized'
        });
      }
    }
  }
]);