/* global angular */
function appendTransform(defaults, transform) {
  defaults = angular.isArray(defaults) ? defaults : [defaults];
  return defaults.concat(transform);
}
angular.module('PricePoint')
  .factory('CalculatorData', ['$resource', '$http', '$q', function($resource, $http, $q) {
    var quoteData = {},
      result = {
        getOriginAgents: function(scope, callback) {
          return getAgents(scope, 'Origin', callback);
        },
        getDestinationAgents: function(scope, callback) {
          return getAgents(scope, 'Destination', callback);
        },
        getFreightAgents: function(scope, callback) {
          return getAgents(scope, 'Freight', callback);
        },
        getOriginPrices: function(scope, callback) {
          return getPrices(scope, 'Origin', callback);
        },
        getDestinationPrices: function(scope, callback) {
          return getPrices(scope, 'Destination', callback);
        },
        getFreightPrices: function(scope, callback) {
          return getPrices(scope, 'Freight', callback);
        },
        getAgentDetails: function(agentId, serviceType, callback) {
          return $http({
            method: 'GET',
            url: ['/details/agent/', agentId, '/', serviceType].join(''),
            cache: true,
          }).success(function(data) {
            callback(data);
          });
        },
        getTariffSupplements: function(agentId, marketId, callback) {
          return $http({
            method: 'GET',
            url: ['/details/supplements/', agentId, '/', marketId].join(''),
            cache: true,
          }).success(function(data) {
            callback(data);
          });
        },
        getMarketDetails: function(marketId, callback) {
          return $http({
            method: 'GET',
            url: ['/details/market/', marketId].join(''),
            cache: true,
          }).success(function(data) {
            callback(data);
          });
        },
        setQuoteData: function(data) {
          quoteData = data;
        },
        getQuoteData: function() {
          return quoteData;
        },
        locations: $resource('/locations/:id', {
          id: '@id'
        }, {
          'get': {
            method: 'GET',
            cache: true
          },
          'query': {
            method: 'GET',
            cache: true,
            isArray: true,
            transformResponse: appendTransform($http.defaults.transformResponse, function(data) {
              var locations = data && data.map(function(item) {
                item.value = item.name.toLowerCase();
                return item;
              });
              return locations;
            })
          }
        }),
        ports: $resource('/ports/:id', {
          id: '@id'
        }, {
          'get': {
            method: 'GET',
            cache: true
          },
          'query': {
            method: 'GET',
            cache: true,
            isArray: false,
            transformResponse: appendTransform($http.defaults.transformResponse, function(data) {
              return data && data.reduce(function(ports, port) {
                ports[port.id] = port.name;
                return ports;
              }, {});
            })
          }
        }),
        
        getAccounts: function(callback) {
          $http({
            method: 'GET',
            url: '/accounts/user/',
            cache: true
          }).success(function(data) {
            return callback(data);
          });
        },
        
        associations: $resource('/associations/:id', {
          id: '@id'
        }, {
          'get': {
            method: 'GET',
            cache: true
          },
          'query': {
            method: 'GET',
            cache: true,
            isArray: false,
            transformResponse: appendTransform($http.defaults.transformResponse, function(data) {
              return {
                "1": {
                  "name": "FIDI",
                  "selected": false
                },
                "3": {
                  "selected": false,
                  "name": "IAM"
                },
                "4": {
                  "selected": false,
                  "name": "LACMA"
                }
              };
            })
          }
        }),
        certifications: $resource('/certifications/:id', {
          id: '@id'
        }, {
          'get': {
            method: 'GET',
            cache: true
          },
          'query': {
            method: 'GET',
            cache: true,
            isArray: false,
            transformResponse: appendTransform($http.defaults.transformResponse, function(data) {
              return {
                "1": {
                  "name": "FAIM",
                  "selected": false
                },
                "11": {
                  "selected": false,
                  "name": "RIM"
                },
                "8": {
                  "selected": false,
                  "name": "ISO 9001"
                }
              };
            })
          }
        }),
        currencySymbols: {
          'USD': '$', // US Dollar
          'EUR': '€', // Euro
          'CRC': '₡', // Costa Rican Colón
          'GBP': '£', // British Pound Sterling
          'ILS': '₪', // Israeli New Sheqel
          'INR': '₹', // Indian Rupee
          'JPY': '¥', // Japanese Yen
          'KRW': '₩', // South Korean Won
          'NGN': '₦', // Nigerian Naira
          'PHP': '₱', // Philippine Peso
          'PLN': 'zł', // Polish Zloty
          'PYG': '₲', // Paraguayan Guarani
          'THB': '฿', // Thai Baht
          'UAH': '₴', // Ukrainian Hryvnia
          'VND': '₫', // Vietnamese Dong
        },
        currencies: $resource('/currencies/:id', {
          id: '@id'
        }, {
          'get': {
            method: 'GET',
            cache: true
          },
          'query': {
            method: 'GET',
            cache: true,
            isArray: true
          }
        }),
        volumeUnitOptions: [{
          name: 'Cubic Foot',
          value: 'cft'
        }, {
          name: 'Cubic Meter',
          value: 'cbm'
        }, ],
        weightUnitOptions: [{
          name: 'Pounds',
          value: 'lb'
        }, {
          name: 'Kilograms',
          value: 'kg'
        }],
        containerSizeOptions: [{
          name: '20',
          value: '20'
        }, {
          name: '40',
          value: '40'
        }, {
          name: '40HC',
          value: '40hc'
        }],
        tariffTypeOptions: [{
          name: 'FCL (Loose)',
          value: 'FCL_L'
        }, {
          name: 'FCL (Cased)',
          value: 'FCL_C'
        }, {
          name: 'LCL',
          value: 'LCL'
        }, {
          name: 'Air',
          value: 'Air'
        }, {
          name: 'Road',
          value: 'Road'
        }],
        statusOptions: [{
          name: 'Booked',
          value: 'Booked'
        }, {
          name: 'Pending',
          value: 'Pending'
        }, {
          name: 'Cancelled',
          value: 'Cancelled'
        }]
      };

    function getPrices(scope, serviceType, callback) {
      var quoteRequested = scope.quoteRequested,
        params = {
          weight: quoteRequested.weight,
          volume: quoteRequested.volume,
          weightUnits: quoteRequested.weightUnits,
          volumeUnits: quoteRequested.volumeUnits,
          currencyId: quoteRequested.currencyId,
          includeTHC: true,
          tariffType: quoteRequested.tariffType,
          serviceType: serviceType,
          containerSize: quoteRequested.containerSize,
          destinationPorts: quoteRequested.destinationLocation ? quoteRequested.destinationLocation.Ports : [],
          originPorts: quoteRequested.originLocation ? quoteRequested.originLocation.Ports : []
        };
      switch (serviceType) {
        case 'Origin':
          params.locationId = quoteRequested.originLocation.id;
          break;
        case 'Destination':
          params.locationId = quoteRequested.destinationLocation.id;
          break;
      }
      return $http({
        method: 'GET',
        url: '/price',
        params: params,
        cache: true,
      }).success(function(data) {
        return callback(data);
      });
    }

    function getAgents(scope, serviceType, callback) {
      var quoteRequested = scope.quoteRequested,
        params = {
          tariffType: quoteRequested.tariffType,
          serviceType: serviceType,
          destinationPorts: [],
          originPorts: []
        };

      for (var key in scope.selectedOriginPorts) {
        if (scope.selectedOriginPorts[key].selected) {
          params.originPorts.push(key);
        }
      }
      for (var key in scope.selectedDestinationPorts) {
        if (scope.selectedDestinationPorts[key].selected) {
          params.destinationPorts.push(key);
        }
      }
      switch (serviceType) {
        case 'Origin':
          params.locationId = quoteRequested.originLocation.id;
          params.ports = params.originPorts;
          break;
        case 'Destination':
          params.locationId = quoteRequested.destinationLocation.id;
          params.ports = params.destinationPorts;
          break;
      }
      return $http({
        method: 'GET',
        url: '/details/agents/',
        params: params,
        cache: true,
        transformResponse: appendTransform($http.defaults.transformResponse, function(data) {
          return data.reduce(function(details, detail) {
            detail.selected = true;
            details[detail.id] = detail;
            return details;
          }, {});
        })
      }).success(function(data) {
        callback(data);
      });
    }
    return result;
  }]);