angular.module('PricePoint').directive('locationSelect', ['$compile', '$templateRequest', 'CalculatorData',
    function($compile, $templateRequest, CalculatorData) {
        return {
            restrict: 'E',
            scope: {
                selectedItem: '=location',
                label: '=',
                disabled: '=',
                locationChanged: '='
            },
            controller: ['$scope', function($scope) {
                var self = this;
                self.locations = CalculatorData.locations.query();
                self.querySearch = querySearch;
                self.selectedItem = null;
                self.searchText = null;
                self.itemChange =  function() {
                    $scope.locationChanged = true;
                };
                self.textChange = function() {};
                self.clear = function() {
                    self.searchText = "";
                };

                function querySearch(query) {
                    var results = query ? self.locations.filter(createFilterFor(query)) : self.locations;
                    return results;
                }

                function createFilterFor(query) {
                    var lowercaseQuery = angular.lowercase(query);
                    return function filterFn(location) {
                        return (location.value.indexOf(lowercaseQuery) === 0);
                    };
                }
            }],
            controllerAs: 'locationSelectCtrl',
            link: function(scope, element, attrs) {
                $templateRequest('location-select.html').then(function(html) {
                    element.append($compile(html.replace('replaceThis', attrs.label))(scope));
                });
            }
        };
    }
]);