/* global angular */
angular.module('PricePoint')
  .controller('MainViewController', ['$scope', 'UserFactory','$rootScope','$location',
    function($scope, UserFactory,$rootScope, $location) {
      var controller = this;

      UserFactory.getUser().then(function success(response) {
        $rootScope.loggedInUser = response.data;
      });

      controller.login = function(username, password) {
        UserFactory.login(username, password).then(function success(response) {
          $rootScope.loggedInUser = response.data.user;
          controller.loginError = null;
          $location.path('/');
        }, function(response) {
          controller.loginError = response.data;
        });
      };

      controller.logout = function() {
        UserFactory.logout();
        $rootScope.loggedInUser = null;
        $location.path('/login');
      };


    }
  ]);
