/* global angular */
angular.module('PricePoint')
  .controller('CalculatorViewController', ['$scope', 'CalculatorData', '$mdDialog', '$location', '$timeout', '$rootScope', 'RatingService',
    function($scope, CalculatorData, $mdDialog, $location, $timeout, $rootScope, RatingService) {
      var quoteData = CalculatorData.getQuoteData();
      $scope.volumeUnitOptions = CalculatorData.volumeUnitOptions;
      $scope.weightUnitOptions = CalculatorData.weightUnitOptions;
      $scope.tariffTypeOptions = CalculatorData.tariffTypeOptions;
      $scope.containerSizeOptions = CalculatorData.containerSizeOptions;
      $scope.selectedOriginPort = -1;
      $scope.selectedDestinationPort = -1;
      $scope.currencies = CalculatorData.currencies.query(function(data) {
        $scope.currencies = data;
      });
      $scope.quoteRequested = quoteData.quoteRequested || {
        originLocation: null,
        destinationLocation: null,
        volume: 1,
        weight: 1,
        weightUnits: 'lb',
        volumeUnits: 'cft',
        tariffType: 'FCL_L',
        containerSize: '20',
        currencyId: 1,
        accountId: null,
        locationChanged: false
      };
      $scope.quoteRequested.calculatedCurrencyId = $scope.quoteRequested.currencyId;
      $scope.ports = CalculatorData.ports.query(function(data) {
        $scope.ports = data;
      });
      $scope.saveRating = function(agentId, rating) {
        return RatingService.saveUserRating(agentId, {
          userRating: rating
        }).then(function(response) {

        });
      };
      $scope.associations = quoteData.associations || CalculatorData.associations.query(function(data) {
        $scope.associations = data;
        $scope.selectedAssociations = data.reduce ? data.reduce(function(associations, association) {
          associations[association] = {
            selected: true
          };
        }) : {};
      });
      $scope.certifications = quoteData.certifications || CalculatorData.certifications.query(function(data) {
        $scope.certifications = data;
        $scope.selectedCertifications = data.reduce ? data.reduce(function(certifications, certification) {
          certifications[certification] = {
            selected: true
          };
        }) : {};
      });
      $scope.accounts = CalculatorData.getAccounts(function(data) {
        $scope.accounts = data;
      });
      $scope.selectedOriginPorts = quoteData.selectedOriginPorts;
      $scope.selectedDestinationPorts = quoteData.selectedDestinationPorts;
      $scope.originAgents = quoteData.originAgents;
      $scope.destinationAgents = quoteData.destinationAgents;
      $scope.freightAgents = quoteData.freightAgents;
      $scope.ratings = {};

      function rating() {
        return this.userRating ? ((this.average * this.users) + this.userRating) / (this.users + 1) : this.average;
      }

      $scope.originAgents && Object.keys($scope.originAgents).forEach(function(id) {
        var agent = $scope.originAgents[id];
        if (!(id in $scope.ratings)) {
          $scope.ratings[id] = agent.ratings;
          $scope.ratings[id].rating = rating;
        }
      });

      $scope.freightAgents && Object.keys($scope.freightAgents).forEach(function(id) {
        var agent = $scope.freightAgents[id];
        if (!(id in $scope.ratings)) {
          $scope.ratings[id] = agent.ratings;
          $scope.ratings[id].rating = rating;
        }
      });

      $scope.destinationAgents && Object.keys($scope.destinationAgents).forEach(function(id) {
        var agent = $scope.destinationAgents[id];
        if (!(id in $scope.ratings)) {
          $scope.ratings[id] = agent.ratings;
          $scope.ratings[id].rating = rating;
        }
      });

      $scope.status = 'Pending';
      $scope.statusOptions = CalculatorData.statusOptions;
      $scope.file = {
        name: 'Demo User',
        number: '<Not Saved>',
        saved: false,
        updatedAt: Date.now()
      };
      $scope.saveFile = function() {
        $timeout(function() {
          $scope.file.saved = true;
          $scope.file.updatedAt = Date.now();
          if ($scope.file.number == '<Not Saved>')
            $scope.file.number = Math.floor(Math.random() * 90000) + 10000;
        }, 1000);
      };
      $scope.fileSectionEditable = false;
      $scope.filterCollapsed = true;

      function _assignAgents(data, agents, changeInSelectedPorts) {
        if (changeInSelectedPorts && agents) {
          var previousIds = Object.keys(agents),
            previouslySelectedIds = previousIds
            .filter(function(id) {
              return agents[id].selected;
            });
          agents = data;
          for (var id in agents) {
            agents[id].selected = (previousIds.indexOf(id) == -1 || previouslySelectedIds.indexOf(id) > -1);
          }
        }
        else {
          agents = data;
        }

        agents && Object.keys(agents).forEach(function(id) {
          var agent = agents[id];
          if (!(id in $scope.ratings)) {
            $scope.ratings[id] = agent.ratings;
            $scope.ratings[id].rating = rating;
          }
        });

        return agents;
      }

      function _setAgentsOnOriginChange(newValue, changeInSelectedPorts) {
        if (newValue) {
          CalculatorData.getOriginAgents($scope, function(data) {
            $scope.originAgents = _assignAgents(data, $scope.originAgents, changeInSelectedPorts);
          });
          CalculatorData.getFreightAgents($scope, function(data) {
            $scope.freightAgents = _assignAgents(data, $scope.freightAgents, changeInSelectedPorts);
          });
        }
        else {
          $scope.freightAgents = null;
          $scope.originAgents = null;
        }
      }

      function _setAgentsOnDestinationChange(newValue, changeInSelectedPorts) {
        if (newValue) {
          CalculatorData.getDestinationAgents($scope, function(data) {
            $scope.destinationAgents = _assignAgents(data, $scope.destinationAgents, changeInSelectedPorts);
          });
          CalculatorData.getFreightAgents($scope, function(data) {
            $scope.freightAgents = _assignAgents(data, $scope.freightAgents, changeInSelectedPorts);
          });
        }
        else {
          $scope.freightAgents = null;
          $scope.destinationAgents = null;
        }
      }


      $scope.$watch('quoteRequested.originLocation.Ports', function(newValue, oldValue) {
        if (newValue != oldValue) {
          $scope.selectedOriginPorts = newValue ? newValue.reduce(function(ports, port) {
            ports[port] = {
              selected: true
            };
            return ports;
          }, {}) : null;
          _setAgentsOnOriginChange(newValue);
        }
      });

      $scope.$watch('quoteRequested.destinationLocation.Ports', function(newValue, oldValue) {
        if (newValue != oldValue) {
          $scope.selectedDestinationPorts = newValue ? newValue.reduce(function(ports, port) {
            ports[port] = {
              selected: true
            };
            return ports;
          }, {}) : null;
          _setAgentsOnDestinationChange(newValue);
        }
      });

      $scope.$watch('selectedDestinationPorts', function(newValue, oldValue) {
        if (newValue != oldValue) {
          _setAgentsOnDestinationChange(newValue, true);
        }
      }, true);
      $scope.$watch('selectedOriginPorts', function(newValue, oldValue) {
        if (newValue != oldValue) {
          _setAgentsOnOriginChange(newValue, true);
        }
      }, true);

      $scope.$watch('quoteRequested.tariffType', function(newValue, oldValue) {
        if (newValue != oldValue) {
          if ($scope.quoteRequested.originLocation)
            _setAgentsOnOriginChange(newValue, true);
          if ($scope.quoteRequested.destinationLocation)
            _setAgentsOnDestinationChange(newValue, true);
        }
      });

      $scope.$watch('quoteRequested.accountId', function(newValue, oldValue) {
        if (newValue != oldValue) {
          if ($scope.quoteRequested.originLocation)
            _setAgentsOnOriginChange(newValue, true);
          if ($scope.quoteRequested.destinationLocation)
            _setAgentsOnDestinationChange(newValue, true);
        }
      });

      $scope.originPrices = null;
      $scope.destinationPrices = null;
      $scope.freightPrices = null;
      $scope.originPrice = null;
      $scope.destinationPrice = null;
      $scope.freightPrice = null;
      $scope.originCollapsed = true;
      $scope.destinationCollapsed = true;
      $scope.freightCollapsed = true;
      $scope.isCalculating = {
        Origin: false,
        Destination: false,
        Freight: false,
      };
      $scope.calculate = function(event) {
        var calculated = false;
        $scope.originPrice = null;
        $scope.destinationPrice = null;
        $scope.freightPrice = null;
        $scope.originCollapsed = true;
        $scope.destinationCollapsed = true;
        $scope.freightCollapsed = true;
        if ($scope.quoteRequested.originLocation) {
          $scope.isCalculating.Origin = true;
          $scope.originPrices = CalculatorData.getOriginPrices($scope, function(data) {
            $scope.originPrices = data;
            $scope.isCalculating.Origin = false;
            if (data && data.length) {
              $scope.originCollapsed = false;
            }
          });
          calculated = true;
        }
        else {
          $scope.originPrices = null;
        }

        if ($scope.quoteRequested.destinationLocation) {
          $scope.isCalculating.Destination = true;
          $scope.destinationPrices = CalculatorData.getDestinationPrices($scope, function(data) {
            $scope.destinationPrices = data;
            $scope.isCalculating.Destination = false;
            if (data && data.length) {
              $scope.destinationCollapsed = false;
            }
          });
          calculated = true;

        }
        else {
          $scope.destinationPrices = null;
        }

        if ($scope.quoteRequested.destinationLocation && $scope.quoteRequested.originLocation) {
          $scope.isCalculating.Freight = true;
          $scope.freightPrices = CalculatorData.getFreightPrices($scope, function(data) {
            $scope.freightPrices = data;
            $scope.isCalculating.Freight = false;
            if (data && data.length) {
              $scope.freightCollapsed = false;
            }
          });
          calculated = true;
        }
        else {
          $scope.freightPrices = null;
        }
        if (calculated) {
          $scope.quoteRequested.locationChanged = false;
          $scope.quoteRequested.calculatedCurrencyId = $scope.quoteRequested.currencyId;
        }
        return calculated;
      };
      $scope.calculate();

      $scope.costItems = {
        'Origin': [],
        'Destination': [],
        'Freight': [],
        'Service': []
      };

      $scope.addCostItem = function(type) {
        var itemsArray = $scope.costItems[type];
        itemsArray.push([{
          name: '',
          value: 0
        }]);
      };

      $scope.removeCostItem = function(type, index) {
        var itemsArray = $scope.costItems[type];
        itemsArray.splice(index, 1);
      };

      $scope.costWidgetHeight = function() {
        var types = ['Origin', 'Destination', 'Freight', 'Service'],
          height = 140 + (28 * types.length);
        types.forEach(function(type) {
          height += $scope.costItems[type].length * 32;
          if ($scope[type.toLowerCase() + 'Price']) {
            height += 32;
          }
        });
        return height;
      };

      $scope.costTotal = function() {
        var types = ['Origin', 'Destination', 'Freight', 'Service'],
          total = 0;
        types.forEach(function(type) {
          $scope.costItems[type].forEach(function(item) {
            if (item.value) {
              total += item.value;
            }
          });
          if ($scope[type.toLowerCase() + 'Price']) {
            total += $scope[type.toLowerCase() + 'Price'].convertedRate;
          }
        });
        return total;
      };
    }
  ]);
