/* global angular */
angular.module('PricePoint').filter('filterPrices', function() {
    return function(array, type, scope) {
        if (array && array.filter) {
            var ports,
                agents,
                portFilter,
                selectedAssociations = Object.keys(scope.associations).filter(function(id) {

                    return scope.associations[id].selected;
                }),
                selectedCertifications = Object.keys(scope.certifications).filter(function(id) {
                    return scope.certifications[id].selected;
                }),
                associationFilter = function(price) {
                    if (!(agents && price.agentDetails.id in agents))
                        return true;
                    var agentAssociations = agents[price.agentDetails.id].associations.map(function(element) {
                        return element.id;
                    });
                    return selectedAssociations.reduce(function(selected, id) {
                        return selected && agentAssociations.indexOf(+id);
                    }, true);
                },
                certificationFilter = function(price) {
                    if (!(agents && price.agentDetails.id in agents))
                        return true;
                    var agentCertifications = agents[price.agentDetails.id].certifications.map(function(element) {
                        return element.id;
                    });
                    return selectedCertifications.reduce(function(selected, id) {
                        return selected && agentCertifications.indexOf(+id) >= 0;
                    }, true);
                },
                allPassFilter = function(price) {
                    return true;
                },
                portOADAFilter = function(price) {
                    return price.Ports.some(function(port) {
                        return (port in ports) && ports[port].selected;
                    });
                },
                portFreightFilter = function(price) {
                    return ((scope.selectedOriginPorts[price.originPort] && scope.selectedOriginPorts[price.originPort].selected) &&
                        (scope.selectedDestinationPorts[price.destinationPort] && scope.selectedDestinationPorts[price.destinationPort].selected));
                },
                agentFilter = function(price) {
                    return (price.agentDetails.id in agents) && agents[price.agentDetails.id].selected;
                };
            switch (type) {
                case 'Origin':
                    ports = scope.selectedOriginPorts;
                    agents = scope.originAgents;
                    agentFilter = agents ? agentFilter : allPassFilter;
                    portFilter = ports ? portOADAFilter : allPassFilter;
                    break;
                case 'Destination':
                    ports = scope.selectedDestinationPorts;
                    agents = scope.destinationAgents;
                    agentFilter = agents ? agentFilter : allPassFilter;
                    portFilter = ports ? portOADAFilter : allPassFilter;
                    break;
                case 'Freight':
                    agents = scope.freightAgents;
                    agentFilter = agents ? agentFilter : allPassFilter;
                    portFilter = (scope.selectedOriginPorts && scope.selectedDestinationPorts) ? portFreightFilter : allPassFilter;
                    break;
            }

            return array.filter(function(price) {
                    return price.convertedRate;
                }).filter(portFilter)
                .filter(agentFilter)
                .filter(certificationFilter)
                .filter(associationFilter);
        }
        return array;
    };
});