/* global angular */
angular.module('PricePoint').factory('AuthInterceptor', ['AuthTokenFactory','$location',
  function AuthInterceptor(AuthTokenFactory, $location) {
    return {
      request: addToken
    };

    function addToken(config) {
      var token = AuthTokenFactory.getToken();
      if (token) {
        config.headers = config.headers || {};
        config.headers.Authorization = 'Bearer ' + token;
      }
      else
      {
        $location.path('/login');
      }
      return config;
    }
  }
]);