angular.module('PricePoint', ['ngRoute', 'ngMaterial', 'ngMdIcons', 'ngResource'])
    .config(['$routeProvider', '$httpProvider',
        function($routeProvider, $httpProvider) {
            $httpProvider.interceptors.push('AuthInterceptor');
            $routeProvider
                .when('/', {
                    templateUrl: 'calculator-initial.html',
                    controller: 'CalculatorInitialViewController',
                    controllerAs: 'calculatorCtrl'
                })
                .when('/calculator', {
                    templateUrl: 'calculator.html',
                    controller: 'CalculatorViewController',
                    controllerAs: 'calculatorCtrl'
                })
                .when('/agent', {
                    templateUrl: 'agent.html',
                    controller: 'AgentProfileController',
                    controllerAs: 'agentProfileCtrl'
                })
                .when('/login', {
                    templateUrl: 'login.html',
                });;
        }
    ]);