/* global angular */
angular.module('PricePoint').factory('RatingService', ['$http',
  function RatingService($http) {
    return {
      saveUserRating: saveUserRating
    };

    function saveUserRating(agentId, data) {
      return $http({
        method: 'POST',
        url: '/ratings/agent/' + agentId,
        data: data
      });
    }
  }
]);